.detailAbout {
  z-index: 500;
  overflow: hidden;
  height: 100vh;
  padding: 1rem;
  width: 30%;
  left: 0;
  right: 0;
  margin: auto;
  position: fixed;
}

.detailAbout p {
  margin: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 5rem;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 2rem;
  font-weight: 800;
}

.detailAbout .card {
  width: 90%;
  border: 1px greenyellow solid;
  margin: auto;
  position: absolute;
  inset: 0;
  height: 40%;
  border-radius: 10px;
  padding: 1rem;
}

.detailAbout form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 2rem;
  text-align: left;
}

.detailAbout form label {
  color: greenyellow;
  font-family: "poppins", sans-serif;
  font-size: 2rem;
  font-weight: 800;
}

.detailAbout form input {
  padding: 1rem;
  border: none;
  border-radius: 100px;
  color: greenyellow;
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
  background-color: #1E1E1E;
  -moz-appearance: textfield;
}

.detailAbout form input[type="submit"] {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1rem 2rem;
  margin: auto;
  font-size: 1.5rem;
  cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .detailAbout {
    width: 100%;
  }
  .detailAbout p {
    position: absolute;
    top: 5rem;
    left: -4rem;
    right: 0;
    margin: auto;
    width: 100%;
  }
  .detailAbout .card {
    max-width: 70%;
    left: -3.5rem;
  }
}
